.simulator-panel {
    width: 400px;
    height: 100%;
    border-right: 2px solid #e0e0e0;
    display: flex;
    flex-direction: column;
    background-color: #f5f5f5;

    .simulator-header {
        padding: 16px;
        background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
        color: white;
        display: flex;
        justify-content: space-between;
        align-items: center;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);

        h3 {
            margin: 0;
            font-size: 18px;
            font-weight: 600;
            display: flex;
            align-items: center;
            gap: 8px;
        }

        .simulator-controls {
            display: flex;
            align-items: center;
            gap: 8px;

            .status-badge {
                display: flex;
                align-items: center;
                gap: 4px;
                padding: 6px 12px;
                border-radius: 16px;
                font-size: 12px;
                font-weight: 500;

                mat-icon {
                    font-size: 16px;
                    width: 16px;
                    height: 16px;
                }

                &.running {
                    background-color: rgba(76, 175, 80, 0.2);
                    color: #4caf50;
                    border: 1px solid rgba(76, 175, 80, 0.5);
                }

                &.stopped {
                    background-color: rgba(255, 255, 255, 0.2);
                    color: rgba(255, 255, 255, 0.9);
                    border: 1px solid rgba(255, 255, 255, 0.3);
                }
            }

            button {
                color: white;
            }
        }
    }

    app-chatbot {
        flex: 1;
        overflow: hidden;
    }
}

.editor-panel {
    flex: 1;
    height: 100%;
    overflow: hidden;
}
